body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'MicrobrewSoftOne';
  src: url(https://wetrustplatform.github.io/hosted-fonts/fonts/MicrobrewSoftOne.otf)
    format('opentype');
}

@font-face {
  font-family: 'MicrobrewSoftOneHatch';
  src: url(https://wetrustplatform.github.io/hosted-fonts/fonts/MicrobrewSoftOneHatch.otf)
    format('opentype');
}
